<template>
  <div id="app" class='wrapper'>
    <router-view name="header"></router-view>
    <router-view name="aside"></router-view>
    <transition name="router-fade">
      <router-view></router-view>
    </transition>
  </div>
</template>
<script>
  export default {
    name: 'app'
  }
</script>



